<template>
  <div>
    <van-nav-bar :title="title" left-arrow left-text="返回" @click-left="$router.back()"></van-nav-bar>

    <!-- 如果被一个有限制高度的div包裹起来，那么地址列表里面的新增按钮在手机端是出不来的 -->
    <!-- <div class="list noTabBarList"> -->
      <van-address-list v-model="chosenAddressId" :switchable="isSwitchable" :list="addressList" @select="handleSelect" @add="handleEdit({id:0})" @edit="handleEdit" />
    <!-- </div> -->
  </div>
</template>

<script>
  export default {
    data() {
      return {
        title:'请选择收货地址',
        chosenAddressId: 0,
        isSwitchable:true
      }
    },
    created() {
      if(this.$route.query.from == 'me'){
        this.isSwitchable = false
        this.title = '我的收货地址'
      }
    },
    computed: {
      cart() {
        return this.$store.cart
      },
      orderProductList(){
        return this.cart.filter(item => item.checked)
      },
      addressList() {
        return this.$store.user ? this.$store.user.addressList : []
      }
    },
    mounted () {

    },
    methods: {
      handleEdit(item, index){
        if(item.id > 0){
          this.$store.chosenAddress = this.addressList.find(addr => addr.id === item.id)
        }else{
          this.$store.chosenAddress = {}
        }
        this.$router.push({name:'addressEdit', params:{id:item.id}})
      },
      handleSelect(){
        this.$store.chosenAddress = this.addressList.find(addr => addr.id === this.chosenAddressId)
        console.log(this.$store.chosenAddress.name, '这里不打印移动端就会报错。。。')
        this.$router.back()
      }
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


